/*   portfolio.scss              */
/*-------------------------------*/


article.portfolio {
    position: relative;
    transition: transform 0.8s ease-out;

    & img:hover {
	cursor: pointer;
	//filter: blur(2px) brightness(60%);
	filter: blur(2px) brightness(45%);

	&~figcaption {
	    position: absolute;
	    display: block;
	    width: 100%;
	    top: 45%;
	    text-align: center;
	    color: var(--Theme-Color-White);
	}
    }
}



.portfolio {
    overflow: hidden;
    background-color: var(--Theme-Color-White);
    border: 2px solid var(--Theme-Color-Black);
   // margin-bottom: 8%; //
   // position: relative;

    @media #{$brk-tablet} {
	flex-direction: row;
        flex: 1 1 45%;
        //margin: 0.91145833333333%; // 7 / 768 = 5.5px
	border: 1px solid var(--Theme-Color-Black);
    }

    & img {
	width: 100%;

/*	&:hover {
	    transform: scale(1.02);
	    transition: transform 0.8s ease-out;
	} */
    }
}

