.bio-avatar {
    display: grid;
    //grid-template-columns: 1fr;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
}

.bio-avatar p {
    grid-column: 1/3;
    margin-bottom: 1em;
}

.bio-avatar img {
    grid-column: 1/3;
    width: 38%;
    border: 2px solid var(--Theme-Color-White);
    border-radius: 50%;
    justify-self: center;
    margin-bottom: 1em;
}







.bio-skills {
    & h3 {
	margin-bottom: .5em;
    }
    
    & ul li {
	padding-left: 0.25em;
	list-style: square inside;
	color: var(--Theme-Color);
    }
}
